<template >
  <v-container fluid>
    <v-expansion-panels accordion hover tile v-model="showOptions">
      <v-expansion-panel>
        <v-expansion-panel-header>Options</v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row dense>
            <v-col cols="4">
              <div class="header">Show items:</div>
              <v-checkbox dense v-model="showQuestPickups" label="Quest pickups"></v-checkbox>
              <v-checkbox
                class="indent"
                dense
                :disabled="!showQuestPickups"
                v-model="showQuestItems"
                label="Quest items"
              ></v-checkbox>
              <v-checkbox dense v-model="showHideoutPickups" label="Hideout items"></v-checkbox>
              <br />
              <div class="header">Filter items:</div>
              <v-checkbox dense v-model="hideNonFoundInRaid" label="'Found in raid'"></v-checkbox>
              <v-text-field :clearable="true" label="Quest" v-model="searchQuest"></v-text-field>
              <v-subheader>Look ahead in quest chain</v-subheader>
              <v-slider
                v-model="lookAheadQuest"
                :tick-labels="ticksLabels"
                :max="9"
                :min="0"
                step="1"
                ticks="always"
                tick-size="4"
              ></v-slider>
              <v-subheader>Look ahead in skill and char level</v-subheader>
              <v-slider
                v-model="lookAheadLevel"
                :tick-labels="ticksLabels"
                :max="9"
                :min="0"
                step="1"
                ticks="always"
                tick-size="4"
              ></v-slider>
              <v-text-field :clearable="true" label="Hideout" v-model="searchHideout"></v-text-field>
              <v-text-field clearable label="Item" v-model="searchItem"></v-text-field>
            </v-col>
            <v-col cols="4">
              <div class="header">Show suggestions:</div>
              <v-checkbox dense v-model="showHideout" label="Hideout upgrade"></v-checkbox>
              <v-checkbox dense v-model="showQuests" label="Quests"></v-checkbox>
              <v-checkbox
                class="indent"
                :disabled="!showQuests"
                dense
                v-model="showNonPickupQuests"
                label="Non-pickup quests"
              ></v-checkbox>
            </v-col>
            <v-col cols="4" v-if="fulfilledQuests.length>0 || fulfilledHideout.length>0">
              <div class="header">You might have all requirements for:</div>
              <div v-for="q in fulfilledQuests" :key="q.id">{{q.name}}</div>
              <div v-for="q in fulfilledHideout" :key="q.id">{{q.name}}</div>
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-row dense v-for="(value, item) in requiredItems" :key="item.id">
      <v-col cols="12">
        <div class="header">
          {{value.item.name}}
          <span
            class="italic"
          >({{stocked[value.item.id]&&stocked[value.item.id].raid?stocked[value.item.id].raid:0 + stocked[value.item.id]&&stocked[value.item.id].nonraid?stocked[value.item.id].nonraid:0}}/{{value.raid&&value.raid.amount?value.raid.amount:0 + value.nonraid&&value.nonraid.amount?value.nonraid.amount:0}})</span>
          <a :href="value.item.url" v-if="value.item.url" target="_blank">
            <v-icon>link</v-icon>
          </a>
        </div>
        <ItemBox
          v-for="i in value.raid&&value.raid.amount?value.raid.amount:0"
          :item="item"
          :value="value"
          :raid="true"
          :checked="stocked[value.item.id]?i<=stocked[value.item.id].raid:false"
          :key="item.id+'r'+i"
          :quests="value.raid.quests"
          :hideout="value.raid.hideout"
          @itemToggled="itemToggled"
        />
        <ItemBox
          v-for="i in value.nonraid&&value.nonraid.amount?value.nonraid.amount:0"
          :item="item"
          :value="value"
          :raid="false"
          :checked="stocked[value.item.id]?i<=stocked[value.item.id].nonraid:false"
          :key="item.id+'n'+i"
          :quests="value.nonraid.quests"
          :hideout="value.nonraid.hideout"
          @itemToggled="itemToggled"
        />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ItemBox from "./ItemBox.vue";

export default {
  name: "Stocked",
  components: { ItemBox },
  data: () => ({
    showOptions: 0,
    stocked: {},
    fulfilledQuests: [],
    fulfilledHideout: [],
    showNonPickupQuests: false,
    showQuestItems: false,
    showQuestPickups: true,
    showHideoutPickups: true,
    hideNonFoundInRaid: false,
    showQuests: true,
    showHideout: true,
    searchQuest: "",
    searchHideout: "",
    searchItem: "",
    ticksLabels: ["+1", "+2", "+3", "+4", "+5", "+6", "+7", "+8", "+9", "all"],
    lookAheadQuest: 9,
    lookAheadLevel: 9
  }),
  computed: {
    requiredItems: function() {
      var vm = this;

      var excludedQuests = this.showQuestPickups
        ? this.$store.state.doneQuests.map(a => ({ ...a }))
        : this.staticData.quests
            .filter(q => !q.items.every(i => i.quest))
            .map(h => h.id);

      if (this.lookAheadQuest < 9) {
        var questTree = this.staticData.getQuestTree();

        var b = Object.keys(questTree).map(key => questTree[key].children);
        var b2 = [].concat.apply([], b);
        for (var i = 0; i <= this.lookAheadQuest; i++) {
          b2.forEach(q => {
            if (q.next) {
              q.next.forEach(n => {
                var next = this.staticData.getQuest(n);
                if (next && b2.indexOf(next) === -1) {
                  b2.push(next);
                }
              });
            }
          });
          console.log(
            i,
            b2.map(b => b.id)
          );
        }
        console.log("before", excludedQuests);
        console.log(
          "new",
          this.staticData.quests
            .filter(q => {
              return b2.indexOf(q) === -1;
            })
            .map(q => q.id)
        );
        this.staticData.quests
          .filter(q => {
            return b2.indexOf(q) === -1;
          })
          .map(q => q.id)
          .forEach(id => {
            if (excludedQuests.indexOf(id) === -1) excludedQuests.push(id);
          });
      }

      if (this.lookAheadLevel < 9) {
        var activeQuests = this.staticData.quests.filter(
          q => excludedQuests.indexOf(q.id) === -1
        );
        console.log("activeQuests", activeQuests);

        var activeQuestsWithFulfilledReq = activeQuests.filter(q => {
          console.log("in", q.id);
          if (q.req) {
            return Object.keys(q.req).every(key => {
              if (this.$store.state.levels[key]) {
                var v =
                  this.$store.state.levels[key] + this.lookAheadLevel >=
                  q.req[key];
                console.log(v);
                return v;
              } else {
                console.log("inner", q.id, key, "levels[key] not found");
                return this.lookAheadLevel >= q.req[key];
              }
            });
          } else {
            console.log("out", q.id, "q.req not found");
            return true;
          }
        });

        var activeQuestsWithFulfilledReqId = activeQuestsWithFulfilledReq.map(
          q => q.id
        );
        console.log(
          "activeQuestsWithFulfilledReq",
          activeQuestsWithFulfilledReqId
        );

        activeQuests
          .filter(q => activeQuestsWithFulfilledReqId.indexOf(q.id) === -1)
          .forEach(q => {
            if (excludedQuests.indexOf(q.id) === -1) {
              excludedQuests.push(q.id);
            }
          });
      }

      console.log(excludedQuests);
      var excludedHideouts = this.showHideoutPickups
        ? this.$store.state.doneHideout
        : this.staticData.hideout.map(h => h.id);
      var items = this.staticData.getAllRequiredItems(
        excludedQuests,
        excludedHideouts
      );
      console.log(items);
      if (!this.showQuestItems) {
        Object.keys(items).forEach(function(key) {
          if (items[key].item.quest) delete items[key];
        });
      }
      if (this.hideNonFoundInRaid) {
        Object.keys(items).forEach(function(key) {
          if (!items[key].raid) {
            delete items[key];
          } else {
            if (items[key].nonraid) {
              delete items[key].nonraid;
            }
          }
        });
      }
      if (this.searchHideout) {
        Object.keys(items).forEach(function(key) {
          if (
            (items[key].nonraid &&
              !items[key].nonraid.hideout.some(h =>
                vm.$options.filters
                  .resolveHideout(h)
                  .name.toLowerCase()
                  .includes(vm.searchHideout.toLowerCase())
              )) ||
            (items[key].raid &&
              !items[key].raid.hideout.some(h =>
                vm.$options.filters
                  .resolveHideout(h)
                  .name.toLowerCase()
                  .includes(vm.searchHideout.toLowerCase())
              ))
          ) {
            delete items[key];
          }
        });
      }
      if (this.searchQuest) {
        Object.keys(items).forEach(function(key) {
          if (
            (items[key].nonraid &&
              !items[key].nonraid.quests.some(h =>
                vm.$options.filters
                  .resolveQuest(h)
                  .name.toLowerCase()
                  .includes(vm.searchQuest.toLowerCase())
              )) ||
            (items[key].raid &&
              !items[key].raid.quests.some(h =>
                vm.$options.filters
                  .resolveQuest(h)
                  .name.toLowerCase()
                  .includes(vm.searchQuest.toLowerCase())
              ))
          ) {
            delete items[key];
          }
        });
      }
      if (this.searchItem) {
        Object.keys(items).forEach(function(key) {
          if (!key.toLowerCase().includes(vm.searchItem.toLowerCase())) {
            delete items[key];
          }
        });
      }
      return items;
    }
  },
  methods: {
    itemToggled: function(value) {
      if (value.item) {
        if (!this.stocked[value.item]) {
          this.$set(this.stocked, value.item, {
            raid: 0,
            nonraid: 0
          });
        }
        if (value.raid) {
          this.$set(
            this.stocked[value.item],
            "raid",
            this.stocked[value.item].raid + (value.checked ? -1 : 1)
          );
        } else {
          this.$set(
            this.stocked[value.item],
            "nonraid",
            this.stocked[value.item].nonraid + (value.checked ? -1 : 1)
          );
        }
      }
    },
    checkObjectives: function(stocked) {
      var excludedQuests = this.$store.state.doneQuests;
      var excludedHideouts = this.$store.state.doneHideout;
      if (this.showQuests) {
        console.log(this.showQuests);
        var filteredQuests = this.staticData.quests.filter(
          q => excludedQuests.indexOf(q.id) === -1
        );
        var map = filteredQuests.filter(q => {
          if (!this.showNonPickupQuests && (!q.items || q.items.length == 0)) {
            return false;
          }
          var v = q.items
            .map(i => {
              var v = stocked[i.id]
                ? stocked[i.id][i.raid ? "raid" : "nonraid"]
                  ? stocked[i.id][i.raid ? "raid" : "nonraid"] >= i.amount
                  : false
                : false;
              return v;
            })
            .reduce((acc, cur) => acc && cur, true);
          return v;
        });
        var reduced = map;
        this.fulfilledQuests = reduced;
      } else {
        this.fulfilledQuests = [];
      }
      if (this.showHideout) {
        this.fulfilledHideout = this.staticData.hideout
          .filter(q => excludedHideouts.indexOf(q.id) === -1)
          .filter(q =>
            q.items
              .map(i => {
                var v = stocked[i.id]
                  ? stocked[i.id][i.raid ? "raid" : "nonraid"]
                    ? stocked[i.id][i.raid ? "raid" : "nonraid"] >= i.amount
                    : false
                  : false;
                return v;
              })
              .reduce((acc, cur) => acc && cur, true)
          );
      } else {
        this.fulfilledHideout = [];
      }
    }
  },
  watch: {
    stocked: {
      handler(newVal) {
        this.checkObjectives(newVal);
        this.$store.commit("setStocked", newVal);
      },
      deep: true
    },
    showHideout(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setShowHideout", newVal);
    },
    showQuests(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setShowQuests", newVal);
    },
    showNonPickupQuests(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setShowNonPickupQuests", newVal);
    },
    showQuestItems(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setShowQuestItems", newVal);
    },
    showQuestPickups(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setShowQuestPickups", newVal);
    },
    showHideoutPickups(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setShowHideoutPickups", newVal);
    },
    hideNonFoundInRaid(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setHideNonFoundInRaid", newVal);
    },
    lookAheadQuest(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setLookAheadQuest", newVal);
    },
    lookAheadLevel(newVal) {
      this.checkObjectives(this.stocked);
      this.$store.commit("setLookAheadLevel", newVal);
    },
    showOptions(newVal) {
      this.$store.commit("setShowOptionsChecklist", newVal);
    }
  },
  created: function() {
    this.stocked = this.$store.state.stocked;
    var config = this.$store.state.config;
    var vm = this;
    Object.keys(config).forEach(function(key) {
      vm.$set(vm, key, config[key]);
    });
    this.showOptions = this.$store.state.showOptionsChecklist;
  }
};
</script>
<style>
.header {
  font-weight: bold;
}
.indent {
  padding-left: 10px;
}
.v-input--checkbox {
  height: 20px;
  margin: 0px;
}
a {
  text-decoration: none;
}
.italic {
  font-style: italic;
}
</style>
